@use 'base/variables' as v;
@use 'base/mixins' as m;

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: left;

    align-items: center;

    @include m.tablet{
        justify-content: flex-end;
        
    }
    &__enlace{
        font-size: 1.6rem;
        color: v.$azul;

        &--texto{
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;

            &:hover{
                background-color: v.$azul;
                color: v.$blanco;
                cursor: pointer;
            }
        }

    
    &--numero{
        font-weight: v.$bold;
        display: none;

        @include m.tablet{
            display: inline;
            
        }
    }

    &--actual{
        color: v.$gris-oscuro;
        font-weight: v.$bold;
        display: none;

        @include m.tablet{
            display: inline;
            
        }
    }
}
}