@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion{
    display: flex;
    flex-direction: column;

    @include m.desktop{
       flex-direction: row; 
    }
    &__enlace{
        
        color: v.$blanco;
        font-size: 2rem;
        padding: 2rem;
        font-weight: v.$black;
        text-align: center;

        &--actual,
        &:hover{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}